import React from "react";
import { Select, InputLabel, MenuItem } from "@mui/material"
import styles from "../styles/menu.module.css";
import day from "../img/day.svg";
import week from "../img/week.svg";
import month from "../img/month.svg";
import { useState } from "react";

function Menu() {
  const [view, setView] = useState("month");

  return (
    <nav className={styles.menu}>
      <InputLabel id="view">Visualizao</InputLabel>
      <Select
        className={styles.selectView}
        labelId="view"
        value={view}
        onChange={e => setView(e.target.value)}
      >
        <MenuItem value={"day"}>
          <img className={styles.icon} src={day} alt="imagem calendrio dia" />
          <span className={styles.iconValue}>Dia</span>
        </MenuItem>
        <MenuItem value={"week"}>
          <img className={styles.icon} src={week} alt="imagem calendrio semana"/>
          <span className={styles.iconValue}>Semana</span>
        </MenuItem>
        <MenuItem selected value={"month"}>
          <img className={styles.icon} src={month} alt="imagem calendrio ms" />
          <span className={styles.iconValue}>Ms</span>
        </MenuItem>
      </Select>
      <span className={styles.warning}>*ainda no implementado</span>
    </nav>
  );
}

export default Menu;
